import React, { useState } from "react";
import "./painelDeControle.css";
import { Slider, Box, Typography, Button } from "@mui/material";

function PainelDeControle({ setE, }) {
  return (
    <Box className="painelDeControle">
      <Typography>Top Right</Typography>
      <Slider
        className="top-right"
        size="small"
        max={1024}
        valueLabelDisplay="auto"
        onChange={(e) => {
          setE[0](e.target.value);
        }}
      />

      <Typography>Top Left</Typography>
      <Slider
        className="top-left"
        size="small"
        max={1024}
        valueLabelDisplay="auto"
        onChange={(e) => {
          setE[1](e.target.value);
        }}
      />

      <Typography>Bottom Right</Typography>
      <Slider
        size="small"
        max={1024}
        aria-label="Small"
        valueLabelDisplay="auto"
        onChange={(e) => {
          setE[2](e.target.value);
        }}
      />

      <Typography>Bottom Right</Typography>
      <Slider
        size="small"
        max={1024}
        aria-label="Small"
        valueLabelDisplay="auto"
        onChange={(e) => {
          setE[3](e.target.value);
        }}
      />

      <Button
        color="primary"
        variant="contained"
        onClick={() => {const codigo =`border-radius: ${document.querySelector(".caixa").style.borderTopRightRadius} ${document.querySelector(".caixa").style.borderTopLeftRadius} ${document.querySelector(".caixa").style.borderBottomRightRadius} ${document.querySelector(".caixa").style.borderBottomLeftRadius};`
        navigator.clipboard.writeText(codigo);

        
          
        }}
      >
        Copiar CÃ³digo
      </Button>
    </Box>
  );
}

export default PainelDeControle;
